.main {
  background: $color-grey;

  border-radius: 25px;
  margin-top: 43px;
  margin-bottom: 43px;

  max-width: 1240px;
  margin-right: auto;
  margin-left: auto;
}

.sectionCards {
  padding: 1em;
  background-color: #f6f6f6;
  border-radius: 25px;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 20px;

  .aptCard {
    position: relative;
    margin: 20px;
    overflow: hidden;
    width: 340px;
    height: 340px;
    border-radius: 10px;
  }

  .imgCard {
    width: 340px;
    height: 340px;
    object-fit: cover;
    transition: 1s;
    &:hover {
      transform: scale(1.3);
    }
  }

  .titleCard {
    position: absolute;
    font-size: 18px;
    color: $color-white;
    bottom: 28px;
    left: 10px;
  }
}
