.collapse {
  overflow: hidden;
  transition: 0.4s max-height;
}

.collapse > div {
  overflow: auto;
}

.sample-content {
  background: #ccc;
  padding: 10px;
}

h1,
p {
  margin: 0;
  text-align: center;
}

button {
  padding: 10px;
  display: block;
  background: #333;
  color: white;
  width: 100%;
  border: 0;
  cursor: pointer;
}
