@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{margin:0;font-family:"Montserrat",sans-serif;color:#ff6060}li{list-style-type:none}a{text-decoration:none;color:#ff6060;cursor:pointer}.navigation{margin:26px 0;margin:26;display:flex;justify-content:space-between;align-items:center;max-width:1240px;margin-right:auto;margin-left:auto}.navigation a{font-size:24px;padding:10px;position:relative}.navigation .nav-active::after{content:"";width:85%;height:2px;background:#ff6060;position:absolute;bottom:1px;left:50%;transform:translateX(-50%)}.banner{display:flex;justify-content:center;position:relative}.banner img{object-fit:cover;object-position:center}.banner p{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-size:3em;color:#fff}.footer{position:relative;height:205px;background:#000}.footer img{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.footer p{color:#fff;position:absolute;top:75%;left:50%;transform:translate(-50%, -50%)}.main{background:#f6f6f6;border-radius:25px;margin-top:43px;margin-bottom:43px;max-width:1240px;margin-right:auto;margin-left:auto}.sectionCards{padding:1em;background-color:#f6f6f6;border-radius:25px;display:grid;grid-template-rows:auto;grid-template-columns:1fr 1fr 1fr;grid-column-gap:20px}.sectionCards .aptCard{position:relative;margin:20px;overflow:hidden;width:340px;height:340px;border-radius:10px}.sectionCards .imgCard{width:340px;height:340px;object-fit:cover;transition:1s}.sectionCards .imgCard:hover{transform:scale(1.3)}.sectionCards .titleCard{position:absolute;font-size:18px;color:#fff;bottom:28px;left:10px}.collapse{overflow:hidden;transition:0.4s max-height}.collapse>div{overflow:auto}.sample-content{background:#ccc;padding:10px}h1,p{margin:0;text-align:center}button{padding:10px;display:block;background:#333;color:white;width:100%;border:0;cursor:pointer}

